.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 375px;

  .heading {
    font-family: "Playfair Display", serif;
    font-weight: bold;
    font-size: max(30px, 3vw);
    color: #3e3e3e;
  }
  .content {
    font-size: max(12px, 1.2vw);
    color: #3e3e3e;
  }
}
