.head {
  display: flex;
  width: 100vw;
  align-items: center;
  justify-content: space-between;

  .container1 {
    display: flex;
    justify-self: left;
    align-self: flex-start;
    .icon {
      display: flex;
      width: 70px;
      height: 90px;
      border-radius: 10px;
      margin-left: 7vw;
      margin-right: 7vw;
    }
  }
  .container2 {
    display: flex;
    justify-self: right;
    font-size: 20px;
    margin-right: 6vw;
    margin-left: 6vw;
    padding-top: 0px;
    .fonth1 {
      display: flex;
      flex-wrap: wrap;
      margin: auto 1vw;
    }
    .fonth2 {
      display: flex;
      flex-wrap: wrap;
      margin: auto 1vw;
      color: #feb42b;
    }
  }
}
